.fundo {
    background-image: url(/meusite/static/media/universo4.560647b1.jpg);
    background-attachment: scroll;
    background-size: cover;
    position: absolute;
    z-index: -4;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    opacity: .8;
}

.frase {
    /* background-color: lawngreen; */
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin-top: 30vh;

}

.frase h1 {
    color: white;
    font-family: 'Noto Sans TC', sans-serif;
    font-weight: 100;
    font-size: 45px;
    text-shadow: 0.1em 0.1em 0.2em black;
    text-align: left; 
    margin-left:30vw;
}

.frase h1:nth-child(2) {
    margin-left: 37vw;
}

.frase h1:nth-child(3) {
    margin-left: 42vw;
}

.frase h1:nth-child(4) {
    margin-left: 49vw;
}

@media (max-width: 600px), (max-height: 450px) {
    .fundo {
        display: none
    }

}

.fundo svg {
    font-size: 60px;
    rotate: 180deg;
}

button {
    cursor: pointer;
}

.menu {
    position: fixed;
    display: inline-flex;
    float: center;
    background-color: transparent;
    width: 100vw;
    height: 12vh;
    top: 0;
    font-family: 'Noto Sans TC', sans-serif;
    font-weight: 400;

    transition: all .5s ease;
}

.menu-scrolled {
    position: fixed;
    display: inline-flex;
    float: center;
    width: 100vw;
    height: 12vh;
    top: 0;
    font-family: 'Noto Sans TC', sans-serif;
    font-weight: 400;
    z-index: 5;

    background-color: rgb(0, 0, 0, .8);
    transition: all .5s ease;

}

.menu a {
    color: white;
    font-size: 18px;
    text-transform: uppercase;

    /* text-shadow: 0.1em 0.1em 0.2em black; */
    transition: all .5s ease;
}

.menu-scrolled a {
    color: white;
    text-transform: uppercase;
    font-size: 16px;
    text-shadow: 0.1em 0.1em 0.2em black;
    transition: all .5s ease;
}

.menu a::after {
    display: block;
    content: '';
    border-bottom: solid 3px #4ea8de;
    padding-bottom: 3px;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    transition: -webkit-transform 0.8s ease-in-out;
    transition: transform 0.8s ease-in-out;
    transition: transform 0.8s ease-in-out, -webkit-transform 0.8s ease-in-out;
}

.menu-scrolled a::after {
    display: block;
    content: '';
    border-bottom: solid 3px white;
    padding-bottom: 3px;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    transition: -webkit-transform 0.8s ease-in-out;
    transition: transform 0.8s ease-in-out;
    transition: transform 0.8s ease-in-out, -webkit-transform 0.8s ease-in-out;
}

.menu-scrolled a:hover::after, .menu a:hover::after {
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
}


.menu ul, .menu-scrolled ul {
    list-style:none;
    /* float: right; */
    margin-left: auto;
    margin-right: auto;
    padding: 2%;
}

.menu li {
    float: left;
    padding: 2.2vh 2.5vh;
}

.menu-scrolled li {
    float: left;
    padding: 0vh 2.2vh;
}

.menu-logo {
    display: none;
}

.menu h1, .menu-scrolled h1 {
    display: none;
}

@media (max-width: 600px) {
    .menu, .menu-scrolled {
        display: inline-flex;
        float: right;
        position: fixed;
        background-color: rgb(0, 0, 0, .7);
        height: 12vh;
        z-index: 5;
    }

    .menu li, .menu-scrolled li {
        padding: none;
    }

    .menu ul, .menu-scrolled ul {
        float: right;
        padding: none;
    }

    .menu a, .menu-scrolled a {
        padding: none;
        font-size: 30px;
        
    }

    .hideMenu {
        display: none;
    }

    .showMenu {
        background-image: url(/meusite/static/media/universo4.560647b1.jpg);
        background-attachment: scroll;
        background-size: cover;
        background-position: center;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100vw;
        height: 100vh;
        z-index: -1;
        padding-top: 11vh;
    }

    .showMenu li {
        float: none;
        text-align: center;
        text-transform: uppercase;
        padding: 10px;
        font-weight: 100;
    }

    .showMenu a {
        text-align: center;
    }

    .showMenu a svg {
        display: none;
    }

    .showMenu svg {
        font-size: 50px;
        rotate: 180deg;
    }

    .showMenu h1 {
        display: block;
        font-family: 'Noto Sans TC', sans-serif;
        font-weight: 100;
        text-shadow: 0.1em 0.1em 0.2em black;
        color: white;
        text-align: center;
        margin-top:10vh; 

    }

    .menu-logo svg {
        color: white;
        font-size: 60px;
        padding-top: 2vh;

    }

    .menu-logo {
        display: block;
        width: 98vw;
        text-align: right;
        padding-right: 2vw;
    }

    .menu-logo button {
        background-color: transparent;
        border: none;
    }

