.total {
    margin-top: 5vh;
}

.total h1 {
    font-family: 'Noto Sans TC', sans-serif;
    font-size: 5em;
    text-align: center;
    color: white;
}

.total svg {
    font-size: 15em;
    text-align: center;
    color: white;
    margin-left: 40vw;
}

.total button {
    padding: 10px;
    font-size: 1em;
    font-weight: 700;
    background-color: transparent;
    border: 3px double white;
    border-radius: 3px;
    margin-left: 40vw;
    margin-top: 2vh;
}

.total button a {
    color: white;
}

@media (max-width: 600px) {
    .total {
        margin-top: 20vh;
    }

    .total h1 {
    font-family: 'Noto Sans TC', sans-serif;
    font-size: 3em;
    text-align: center;
    color: white;
    }

    .total svg {
        font-size: 10em;
        text-align: center;
        color: white;
        margin-left: 28vw;
    }

    .total button {
        padding: 10px;
        font-size: 1em;
        font-weight: 700;
        background-color: transparent;
        color: white;
        border: 3px double white;
        border-radius: 3px;
        margin-left: 18vw;
        margin-top: 2vh;
    }
}

@media (max-height: 450px) {
    .total {
        margin-top: 0vh;
    }

    .total h1 {
        font-family: 'Noto Sans TC', sans-serif;
        font-size: 4em;
        text-align: center;
        color: white;
    }

    .total svg {
        font-size: 11em;
        text-align: center;
        color: white;
        margin-left: 36vw;
    }

    .total button {
        padding: 10px;
        font-size: 1em;
        font-weight: 700;
        background-color: transparent;
        color: white;
        border: 3px double white;
        border-radius: 3px;
        margin-left: 34vw;
        margin-top: 2vh;
        margin-bottom: 10vh;
    }
}